<template>
  <view class="message-container">
    <view v-for="(message, index) in notice" :key="index" class="message">{{ message.desc }}</view>
  </view>
</template>


<script>
  import {
    mapState
  } from 'vuex'
  export default {
    computed: {
      ...mapState('message', ['notice'])
    },
    data() {
      return {
        // messages: []
      };
    },
    methods: {
      showWinnerMessage(message) {
        this.messages.push(message);
        setTimeout(() => {
          this.messages.shift();
        }, 2500); // 消息停留时间
      }
    }
  }
</script>

<style lang="scss">
  .message-container {
    position: fixed;
    top: 50%;
    left: 0;
    width: 100%;
    transform: translateY(-50%);
    text-align: center;
  }

  .message {
    display: inline-block;
    background-color: #ffd700;
    padding: 10px 20px;
    border-radius: 5px;
    animation: slide-in 0.5s forwards, slide-out 0.5s forwards 2s;
  }

  @keyframes slide-in {
    0% {
      transform: translateX(-100%);
    }

    100% {
      transform: translateX(0);
    }
  }

  @keyframes slide-out {
    0% {
      transform: translateX(0);
    }

    100% {
      transform: translateX(-100%);
    }
  }
</style>